  <div class="main1" id="main1">
    <el-container>
      <el-header>
        <el-row :gutter="20" type="flex" justify="space-between">
          <el-col :span="14">
            <span>logo  魔方支付</span>
            <el-button-group>
              <el-button type="primary" @click="headerRouteHandle('home')">系统首页</el-button>
              <el-button type="primary" @click="headerRouteHandle('operationcenter')">运营中心</el-button>
