  export default {
    name: 'main1',
    data() {
      return {
        activeName: '',
        isCollapse: false
      }
    },
    methods: {
      handleClick(tab, event) {

      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      headerRouteHandle(ronterName){
        this.$router.push('/'+ronterName)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main1 {
    min-width: 1330px;
    overflow-x: scroll;
  }
  .el-header, .el-footer {
    /*background-color: #B3C0D1;*/
    color: #333;
    background-color: #3fbc9c;
    text-align: left;
  }

  .el-aside {
    overflow: inherit;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .el-button {
    height: 60px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-radio-group {
    display: inherit;
  }
</style>



